const compareVr = (v1, v2) => {
  const v1arr = v1.split('.')
  const v2arr = v2.split('.')
  // if(v1arr.length != v2arr.length) {
  //     plus.nativeUI.alert(``, () => {
  //         force && plus.runtime.quit()
  //     }, "", "")
  //     throw new Error("")
  // }
  const len = v1arr.length > v2arr.length ? v1arr.length : v2arr.length;
  for (let index = 0; index < len; index++) {
    const v1pos = v1arr[index] || 0;
    const v2pos = v2arr[index] || 0;
    if (Number(v1pos) > Number(v2pos)) {
      return 'big'
    }
    if (Number(v1pos < Number(v2pos))) {
      return 'small'
    }
  }

  return 'same'
}

window.compareVr = compareVr

export const checkUpdate = (appid, {
  currentVersion,
  minVersion,
  packageAddress,
  updatePackageAddress
}) => {
  try {
    if (window.plus) {
      // const cv = currentVersion
      const mv = minVersion
      const apv = appid
      // const lcv = plus.storage.getItem('downloadedCv') || ''

      // console.log(`cv:${cv} mv:${mv} apv:${apv} lcv:${lcv}`);

      function installHandle(isUpdated) {
        if (isUpdated) {
          plus.runtime.restart()
        } else {
          plus.nativeUI.alert(``, () => {
            plus.runtime.quit()
          }, "", "")
        }
      }


      function update() {
        plus.nativeUI.showWaiting("...");
        window.h5p.downWgt(updatePackageAddress, function (isDownloaded, next) {
          if (isDownloaded) {
            // 
            next(isUpdated => installHandle(isUpdated))
          } else {
            plus.nativeUI.alert(``, () => {
              plus.runtime.quit()
            }, "", "")
          }
        })
      }

      if (compareVr(apv, mv) === 'small') {
        update()
        return true
      }

      return false
    }
  } catch (error) {
    console.error("", error);
  }

}
